<template>
  <div class="out-frame" :id="singleId">
    <frame-header :info="information"></frame-header>
    <!---->
    <div class="model-content">
      <simple-list :scroll-data ="mockData" :configParams ="configOption"></simple-list>
    </div>
  </div>
</template>

<script>
  import simpleList from '../components/listSimple.vue'
  export default {
    name: 'scrollList',
    components:{
      'simple-list':simpleList
    },
    beforeMount(){
      const originData = [{name: 'css', limit: 173},
        {name: 'React', limit: 46},
        {name: 'cors', limit: 25},
        {name: '', limit: 18},
        {name: 'CSS', limit: 15},
        {name: 'Content-type multipart/form-data', limit: 14},
        {name: 'webpack', limit: 11},
        {name: 'Fetch', limit: 9},
        {name: 'VUE', limit: 9}];
      let total = originData.reduce((prev,cur)=>{
        return ( prev.limit || prev ) + cur.limit;
      });
      this.mockData={
        unit:'',
        blocks:originData.map((t)=>{
            t.percent = (t.limit * 100 /total).toFixed(0) + '%';
            return t;
        })

      };
      this.configOption ={
        intervalTime : 5000,
        highRankColor: '#f249e5',
        fillColor:'background-image: linear-gradient(to right,#00d596,#00bbf0)'
      }
    },
/*    created(){
      console.log(this);
      debugger;
    },
    beforeMount(){
      console.log(this);
      debugger;
    },*/
    mounted() {
    },
    data() {
      return {
        singleId: 'radar' + Math.ceil(Math.random() * 10000),
        msg: 'small model',
        information: {
          name: 'Top10',
          link: 'http:localhost:8080',
          dataSource: 'closertb.site'
        }
      }
    },
    methods: {
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  $bgColor: '#013bba';
  $singleColor: '#1f5fd3';
  $triangleColor: '#003596';

  h1, h2 {
    font-weight: normal;
  }

  ul, li {
    list-style-type: none;
    padding: 0;
  }
</style>



