<template>
  <div class="out-frame" :id="singleId">
    <frame-header :info="information"></frame-header>
    <!---->
    <div class="model-content">
      <div class="analyse-chart" id="resourceCountChart"></div>
    </div>
  </div>
</template>

<script>
  import {addSeparator,AutoShowTip,strongText} from '../js/tools'

  export default {
    name: 'favorCity',
    mounted() {
      this.initChart(this.data);
    },
    data() {
      return {
        singleId: 'radar' + Math.ceil(Math.random() * 10000),
        msg: 'small model',
        information: {
          name: '',
          link: 'http:localhost:8080'
        },
        data:[ 34774, 41733, 43005, 45826]
      }
    },
    methods: {
      initChart: function (data) {
        let target = document.getElementById(this.singleId).querySelector('#resourceCountChart'),
          option = {
            tooltip: {
              trigger: 'axis',
              backgroundColor: 'rgba(0,0,0,.8)',
              textStyle: {
                color: '#b4d1e6'
              },
              formatter: function (val) {
                return val[0].name + ':<span style="color:#ffbf00;font-weight: bold;font-size:14px;padding: 0 5px;" >' + addSeparator(val[0].data) + '</span>'
              }
            },
            grid: {
              top: '15%',
              left: '6%',
              right: '8%',
              bottom: '6%',
              containLabel: true
            },
            title:{
              show:true,
              left:'center',
              top:15
            },
            xAxis: {
              type: 'value',
              /*
                  minmaxsplitNumber=3,
                  interval
              */
              /*          min:dataMin,
                        max:dataMax,
                        //splitNumber:(dataMax-dataMin)/3,
                        interval:(dataMax-dataMin)/3,*/
              splitLine: {
                show: true,
                lineStyle: {
                  type: 'dotted',
                  color: '#1a2e60'
                }
              },
              axisLabel: {
                show: true,
                textStyle: {
                  fontSize: 14,
                  color: '#c6dff1'
                }
              },
              axisLine: {
                show: true
              },
              axisTick: {
                show: true
              }
            },
            yAxis: {
              type: 'category',
              data: [ '','','', ''].reverse(),
              splitArea: {
                show: true,
                areaStyle: {
                  color: ["rgba(0,29,88,0.5)", "rgba(0,0,0,0)"]
                }
              },
              axisLabel: {
                show: true,
                textStyle: {
                  color: '#c6dff1',
                  fontSize: 14
                },
                formatter: function (val) {
                  return val.replace(/(\S{4})(?=\S{2,})/g, '$1' + '\n');
                }
              },
              axisLine: {
                lineStyle: {
                  type: 'dotted',
                  color: '#1e3876'
                }
              }
            },
            series:[
              {
                name: '',
                type: 'bar',
                /**/
                barWidth: '35.7%',
                itemStyle: {
                  normal: {
                    //
                    barBorderRadius: 3,
                    label: {
                      show: true,//
                      textStyle: {
                        fontWeight: 'bolder',
                        fontSize: '12',
                        fontFamily: '',
                      }
                    },
                    color: '#3d98ff'
                  }
                },
                label: {
                  normal: {
                    show: true,
                    position: 'right',
                    textStyle: {
                      color: '#c6dff1',
                      fontSize: 14
                    },
                    formatter: function (val) {
                      return addSeparator(val.value);
                    }

                  }
                },
                data: data
              }]
          },
        myChart = this.$echarts.init(target);
        strongText(option,{name:'2017',value:data.reduce((prev,cur)=>{return prev+cur;}),unit:''});
        myChart.setOption(option);
        this.$echarts.AutoShowTip(myChart, option, 3000);
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  $bgColor: '#013bba';
  $singleColor: '#1f5fd3';
  $triangleColor: '#003596';

  h1, h2 {
    font-weight: normal;
  }

  ul, li {
    list-style-type: none;
    padding: 0;
  }



</style>



