<template>
  <div class="out-frame" :id="singleId">
    <frame-header :info="information"></frame-header>
    <!---->
    <div class="model-content">
      <div class="analyse-chart" id="mapChart"></div>
    </div>
  </div>
</template>

<script>
  import {addSeparator} from '../js/tools';
  import {themeColor} from '../theme/base';
  export default {
    name: 'favorScenic',
    mounted() {
      const datas = [{name: "", value: 800},
        {name: "", value:600},
        {name: "", value:500},
        {name: "", value: 300}];
      this.initChart(datas);
    },
    data() {
      return {
        singleId: 'radar' + Math.ceil(Math.random() * 10000),
        msg: 'small model',
        information: {
          name: '',
          link: 'http:localhost:8080',
          dataSource: 'smallFlag'
        }
      }
    },
    methods: {
      initChart: function (data) {
        const target = document.getElementById(this.singleId).querySelector('#mapChart'),
          option = {
            tooltip: {
              show: true,
              position:function (point,params,dom,rect,size) {
                return [point[0]-size.contentSize[0]/2,point[1]-size.contentSize[1]-10];
              },
              formatter:function (param) {
                let html = '<div class="tool-tip"><h3 style="color:#d3edff;text-align: center;">'
                  + param.seriesName+'</h3><div style="padding: 3px 0;color:#8bb8e8;text-align: left;">'
                  +param.data.name +
                  ':<label <span style="color:#ffbf00;font-weight: bold;font-size:14px;padding: 0 5px;">'+param.data.value+'</label>' +
                  '</div></div>';
                return html
              }
            },
            visualMap: {
              min: 0,
              max: 1000,
              left: 'left',
              top: 'bottom',
              color:['#ffb414','#f0dd06','#00bbf0'],
              text: ['',''],           // 
              textStyle:{
                color:'white'
              },
              calculable: true
            },
            geo: {
              map: '',
              label: {
                normal: {
                  show: true,
                  textStyle: {
                    color: '#d3edff'
                  },
                  position: ['50%', '50%'],
                  align: 'center',
                  fontSize: 14,
                  fontWeight: 'bold'
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    color: '#d3edff'
                  }
                }
              },
              itemStyle: {
                normal: {
                  areaColor: '#45aaff',
                  borderWidth: 1,
                  borderColor: '#122e41'
                },
                emphasis: {
                  show: false,
                  areaColor: '#0083ef'
                }
              },
              roam: false,
              center: [102.9526, 29.8617],
              zoom: 1,
              /*  */
              regions: []
            },
            series: [{
              type: 'map',
              name:'',
              geoIndex: 0,
              data: [{name:'',value:750},
                {name:'',value:430},
                {name:'',value:300},
                {name:'',value:200},
                {name:'',value:100},
                {name:'',value:80},
                {name:'',value:70},
                {name:'',value:50},
                {name:'',value:60},
                {name:'',value:20},
                {name:'',value:80},
                {name:'',value:20},
                {name:'',value:20},
                {name:'',value:10},
                {name:'',value:10},
                {name:'',value:10},
                {name:'',value:10},
                {name:'',value:8},
                {name:'',value:10},
                {name:'',value:5},
                {name:'',value:5},
                {name:'',value:5}]
            }]
        },
        myChart = this.$echarts.init(target);
        fetch('http://filealiyun.geeker.com.cn/ued/map/regionJson/510000.json', {
          method:'get',
          mode:'cors'
        }).then((response)=>{
          return response.json();
        }).then((data)=>{
          this.$echarts.registerMap('', data);
          myChart.setOption(option);
          this.$echarts.AutoShowTip(myChart, option, 3000);
        });

      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  $bgColor: '#013bba';
  $singleColor: '#1f5fd3';
  $triangleColor: '#003596';

  h1, h2 {
    font-weight: normal;
  }

  ul, li {
    list-style-type: none;
    padding: 0;
  }
  .out-frame{
    position: relative;
    width:950px;
    height: 710px;
  }
</style>



